export default {
  data() {
    return {
      category: "all",
      categoryList: [
        {
          id: 1,
          label: "all",
        },
        {
          id: 2,
          label: "包包",
        },
        {
          id: 3,
          label: "月曆",
        },
        {
          id: 4,
          label: "帽子",
        },
        {
          id: 5,
          label: "馬克杯",
        },
        {
          id: 6,
          label: "手機殼",
        },
        {
          id: 7,
          label: "撲克牌",
        },
        {
          id: 8,
          label: "娃娃",
        },
        {
          id: 9,
          label: "T-shirt",
        },
      ],
      productData: [],
    };
  },
  computed: {
    filterProductData() {
      return (data) => {
        if (this.category === "all") {
          return data;
        } else {
          return data.filter((item) => item.chCategory === this.category);
        }
      };
    },
  },
  methods: {
    getData() {
      this.axios({
        method: "get",
        url: "https://raw.githubusercontent.com/Rosette-xii/vue-mysite/main/src/assets/static/data.json",
      })
        .then((res) => {
          this.productData = res.data.productData;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    filterCategory(item) {
      this.category = item.label;
      this.goTop();
    },
    goTop() {
      window.scrollTo(0, 0);
    },
  },
  mounted() {
    this.getData();
  },
};
